import React, { useState,useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Input } from '@components/libs/Forms';
import Button from '@components/libs/Button';
import SojiBaseInit from '@modules/SojiBase';
import styled from 'styled-components'
import Cookies from 'js-cookie';
import '../../index.css';
import GoogleLogin from '../GoogleLogin';
import useUserDetails from '@components/Hooks/useUserDetails'

const SendButton = styled(Button)`
    width: 60%;
    height: 40px;
`

export default function Login({ pageHandler, embedToGetUserToken }) {
    const navigate = useNavigate();
    const useUserDetail = useUserDetails()

    const [state, setState] = useState({
        email: '',
        password: '',
        onRequest: false,
        error: false
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const login = async () => {
        if (typeof Cookies.get('COMPANY_SOJI_uft_token') != 'undefined') {
            const SOJIBase = await SojiBaseInit;
            await SOJIBase.deleteCookie();
            useUserDetail.set(false);
        }
        stateHandler('onRequest', true);
        const payload = {
            email: state.email,
            password: state.password,
        };
        const SojiBase = await SojiBaseInit;
        const getter = await SojiBase.post('v1/authe/login', payload);
        getter.on('static', (response) => {
            stateHandler('onRequest', false);
            if(response.response.status == 403){
                stateHandler('error', response.response.data)
            }else {
                const data = response.response.data;
                if (data.token) {
                    Cookies.set('SOJI_uft_token', data.token);
                    useUserDetail.set(data)
                    if (typeof Cookies.get('SOJI_uft_token') != 'undefined') {
                        navigate('/')
                        if (embedToGetUserToken) {
                            embedToGetUserToken(data.token);
                        }
                    }
                }
            }
          
        });
    };

    return (
        <>
            <div className="authe_container">
                <div className="title">Sign In</div>
                <GoogleLogin text="Login with Google" embedToGetUserToken={embedToGetUserToken} />
                <Input placeholder="Email" title="Email" onChange={(e) => stateHandler('email', e.target.value)} />
                <Input
                    placeholder="Password"
                    title="Password"
                    type="password"
                    onChange={(e) => stateHandler('password', e.target.value)}
                />
                <div className="authe_action_box">
                    <p>
                        Have no account?
                        <a
                            onClick={() => pageHandler('pageName', 'signup')}
                            className="text_default_color text_bold cursor_pointer"
                        >
                            Sign Up here
                        </a>
                    </p>
                    <p>
                        <a
                            onClick={() => pageHandler('pageName', 'forgotPassword')}
                            className="text_default_color cursor_pointer"
                        >
                            Forgot Password?
                        </a>
                    </p>
                </div>
                {state.error && <p style={{color: 'red'}}>{state.error}</p>}
                <div className="button_container">
                    <SendButton className="button" title="Login" loading={state.onRequest} onClick={login} />
                </div>
            </div>
        </>
    );
}
