import React, { useState, useEffect } from 'react';
import { Input, Select } from '@components/libs/Forms';
import { useNavigate } from 'react-router-dom';
import Button from '@components/libs/Button';
import SojiBaseInit from '@modules/SojiBase';
import styled from 'styled-components';
import Cookies from 'js-cookie';
import '../../index.css';
import GoogleLogin from '../GoogleLogin';
import useUserDetails from '@components/Hooks/useUserDetails';
import Preloader from '@components/preloader';

const SendButton = styled(Button)`
    width: 60%;
    height: 40px;
`;

export default function SignUp({ pageHandler, embedToGetUserToken }) {
    const navigate = useNavigate();
    const useUserDetail = useUserDetails();

    const [state, setState] = useState({
        firstName: '',
        lastName: '',
        phoneNumber: '',
        email: '',
        password: '',
        confirmPassword: '',
        onRequest: false,
        country: ''
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const signup = async () => {
        if (typeof Cookies.get('COMPANY_SOJI_uft_token') != 'undefined') {
            const SOJIBase = await SojiBaseInit;
            await SOJIBase.deleteCookie();
            useUserDetail.set(false);
        }
        stateHandler('onRequest', true);
        const payload = {
            firstName: state.firstName,
            lastName: state.lastName,
            phoneNumber: state.phoneNumber,
            email: state.email,
            password: state.password,
            countryID: state.country
        };
        const SojiBase = await SojiBaseInit;
        const getter = await SojiBase.post('v1/authe/signup', payload);
        getter.on('static', (response) => {
            stateHandler('onRequest', false);
            const data = response.response.data;
            if (data.token) {
                Cookies.set('SOJI_uft_token', data.token);
                useUserDetail.set(data);
                if (typeof Cookies.get('SOJI_uft_token') != 'undefined') {
                    navigate('/');
                    if (embedToGetUserToken) {
                        embedToGetUserToken(data.token);
                    }
                }
            }
        });
    };

    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.get('v1/utils/countries');
            getter.on('static', (response) => {
                stateHandler('countriesList', response.response.data);
            });
        })();
    }, []);

    return (
        <>
            {state.countriesList ? (
                <div className="authe_container">
                    <div className="title">Sign Up</div>
                    <GoogleLogin text="Signup with Google" embedToGetUserToken={embedToGetUserToken} />
                    <div className="authe_content">
                        <Input
                            placeholder="First Name"
                            title="FIrst Name"
                            onChange={(e) => stateHandler('firstName', e.target.value)}
                        />
                        <Input
                            placeholder="Last Name"
                            title="Last Name"
                            onChange={(e) => stateHandler('lastName', e.target.value)}
                        />
                        <Input
                            placeholder="Phone Number"
                            title="Phone Number"
                            onChange={(e) => stateHandler('phoneNumber', e.target.value)}
                        />
                        <Input
                            placeholder="Email"
                            title="Email"
                            onChange={(e) => stateHandler('email', e.target.value)}
                        />

                        <Select
                            placeholder="Select Country"
                            title="Select Country"
                            onChange={(e) => {
                                stateHandler('country', e.value);
                            }}
                            options={state.countriesList}
                            important
                            value={state.countriesList.filter(function (option) {
                                return option.value === state.country;
                            })}
                        />

                        <Input
                            placeholder="Password"
                            title="Password"
                            type="password"
                            onChange={(e) => stateHandler('password', e.target.value)}
                        />
                        <Input
                            placeholder="Confirm Password"
                            title="Confirm Password"
                            type="password"
                            onChange={(e) => stateHandler('confirmPassword', e.target.value)}
                        />
                    </div>
                    <div className="authe_action_box">
                        <p>
                            Have an account?
                            <a
                                onClick={() => pageHandler('pageName', 'login')}
                                className="text_default_color text_bold cursor_pointer"
                            >
                                Sign In
                            </a>
                        </p>
                    </div>
                    <div className="button_container">
                        <SendButton className="button" title="Sign Up" loading={state.onRequest} onClick={signup} />
                    </div>
                </div>
            ) : (
                <Preloader />
            )}
        </>
    );
}
