import React from 'react';
import styled from 'styled-components';
import { FcGoogle } from 'react-icons/fc';

const Container = styled.div`
    background: #fff;
    box-shadow: 0px 4px 10px rgb(0 0 0 / 2%);
    border-radius: 6px;
    margin: 20px;
    padding: 15px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
`;
const Text = styled.div`
    margin-left: 10px;
`;

export default function LoginWithGoogle({ text, ...props }) {
    return (
        <Container {...props}>
            <FcGoogle size={24} />
            <Text>{text}</Text>
        </Container>
    );
}
